<template>
  <div id="app">
<!--     <img src="./assets/logo.png"> -->
    <!-- 在子组件上添加自定义属性来传递数据给子组件 -->
      <s-header :seller="seller"></s-header>
      <div class="tab border-1px">
        <div class="tab-item">
          <router-link v-bind:to="{path:'/goods'}">商品</router-link>
        </div>
        <div class="tab-item">
          <router-link v-bind:to="{path:'/ratings'}">评论</router-link>
        </div>
        <div class="tab-item">
          <router-link :to="{path:'/seller'}">商家</router-link>
        </div>
      </div>
      <router-view :seller="seller"></router-view>

  </div>
</template>

<script>
    // 引入组件
    // import Hello from './components/Hello';
    import header from './components/header/header';
    import data from './common/json/data.json';
    // const ERR_OK = 0;
    // 导出当前组件

    export default {
        // 命名当前组件
        name: 'app',
        data: function() {
            // 必需声明，不然子组件取不到数据
            return {
                seller: {}
            };
        },
        created() {
            // this.$http.get('/api/seller').then((response) => {
            //   response = response.body;
            //   if (response.erron === ERR_OK) {
            //     this.seller = response.data;
            //   }
            // });
            this.seller = data.seller;
        },
        // 注册hello组件
        components: {
            's-header': header
        }
    };
</script>

<style>
    #app .tab {
        display: flex;
        width: 100%;
        height: 40px;
        position: relative;
        line-height: 40px;
        /*border-bottom: 1px solid rgba(7,17,27,.1);*/
    }
    
    .tab:after {
        display: block;
        left: 0;
        bottom: 0;
        position: absolute;
        width: 100%;
        content: '';
        border-top: 1px solid rgba(7, 17, 27, .1);
    }
    
    .tab .tab-item {
        flex: 1;
        text-align: center;
    }
    
    .tab .tab-item a {
        display: block;
        /*line-height: 14px;*/
        font-size: 14px;
        color: #778593;
    }
    
    .tab .tab-item a.active {
        color: rgb(240, 20, 20);
    }
</style>


